const xxx = "hey";

const _ = require('lodash');
const configData = require('./../datasets/config.json');
//const configData = require('./config');

var i = 0;
var theBaseDeck = [];
var theFinalShuffleDeck = [];
var theDeckAct = { 1: [], 2: [], 3: [] };
_.forEach(configData.cards, function (deck, suit) {
    _.forEach(deck, function (cardNumber) {
        theBaseDeck.push({
            i: i,
            suit: suit,
            number: cardNumber
        });
        i++;
    })
})


_.forEach(_.shuffle(theBaseDeck), function (card) {
    theFinalShuffleDeck.push(card);
});


_.forEach(_.shuffle(theBaseDeck), function (card) {

    cardClone = _.clone(card)
    cardClone.i = cardClone.i + theBaseDeck.length;
    theFinalShuffleDeck.push(cardClone);
});


_.forEach(theFinalShuffleDeck, function (card, j) {
    var deckIndex = 1
    if ((j) % 3 === 0) {
        deckIndex = 1
    }
    else if ((j + 1) % 3 === 0) {
        deckIndex = 2
    }
    else if ((j + 2) % 3 === 0) {
        deckIndex = 3
    }
    theDeckAct[deckIndex].push(card)
});


//console.log(theDeckAct[3].length)

theDeckActClean = [];


_.forEach(theDeckAct[3], function (card, round) {
    if (theDeckAct[1][round + 1] && theDeckAct[2][round + 1] && theDeckAct[3][round + 1]) {
        //console.log(theDeckAct[1][round].suit, theDeckAct[2][round + 1].suit, theDeckAct[3][round + 1].suit)
        //console.log(theDeckAct[1][round + 1].number, theDeckAct[2][round + 1].number, theDeckAct[3][round + 1].number)
   
    theDeckActClean.push({
        left: {
            suit: theDeckAct[1][round].suit,
            suitNext: theDeckAct[1][round+1].suit,
            number: theDeckAct[1][round+1].number,
        },
        center: {
            suit: theDeckAct[2][round].suit,
            suitNext: theDeckAct[2][round+1].suit,
            number: theDeckAct[2][round+1].number,
        }, right: {
            suit: theDeckAct[3][round].suit,
            suitNext: theDeckAct[3][round+1].suit,
            number: theDeckAct[3][round+1].number
        }
    });

}

})

//console.log(theDeckActClean.length)


var thePlanAct = {no1:
_.sample(configData.plans.no1),
no2:_.sample(configData.plans.no2),
no3:_.sample(configData.plans.no3),}

//console.log(thePlanAct)

module.exports = {
    shuffelDeck: function(){
        return {
            plan:thePlanAct,
            deck:theDeckActClean,
            config:configData,

        };
    },
}